// Packages
import Link from 'next/link'
import React, { Component } from 'react'

// Ours
// ...

export default class extends Component {

  static getInitialProps({ type = 'white', logo = 'rfrf' }) {
    const pathLogos = '../../static/img/logos/'
    // logo = pathLogos + 'logo-h-' + type + '.png'

    return {
      logo,
      type
    }
  }

  render() {

    console.log(this.logo);

    return (
      <footer className={ this.props.type }>
        <div className="scontainer">
          <div className="line">
            <div className="x:scol-6">
              <div className="logo">
                <Link href="/">
                  <a>
                    <img src={ this.props.logo } alt="Kodemia"/>
                  </a>
                </Link>
              </div>
            </div>
            <div className="x:scol-6 x:right">
              <div className="social-items">
                <ul>
                  <li>
                    <Link href="https://www.facebook.com/Kodemia-637875623216179/">
                      <a target="_blank">
                        <i className="fab fa-facebook-f"></i>
                      </a>
                    </Link>
                  </li>
                  <li>
                    <Link href="https://twitter.com/kodemiamx">
                      <a target="_blank">
                        <i className="fab fa-twitter"></i>
                      </a>
                    </Link>
                  </li>
                </ul>
              </div>
            </div>
          </div>
        </div>
      </footer>
    )
  }
}



